@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

// project specific CSS goes here
html {
  scroll-behavior: smooth;
}

[x-cloak] {
  display: none;
}

body {
  @apply bg-light overflow-x-hidden overflow-y-auto w-screen min-h-screen flex flex-col text-gray-700 text-base font-poppins scrollbar-thin scrollbar-track-gray-700 scrollbar-thumb-primary relative;
}

.padding-x {
  @apply px-4 md:px-14 lg:px-24 xl:px-44;
}

.padding-x2 {
  @apply px-4 md:px-14 lg:px-24 xl:px-80;
}

.bg-blur {
  @apply backdrop-blur-lg backdrop-filter
}

#tidio-chat-iframe {
  bottom: 70px !important
}

.button-primary {
  @apply bg-primary text-white py-2 px-6 rounded-md
}

.center {
  @apply flex flex-col items-center justify-center
}
